            ></a>
            <a href="https://twitter.com/storm_lor" target="_blank" rel="noopener" aria-label="Twitter"><i class="fab fa-twitter"></i></a>
          </div>
          <div class="mt-2">
            <p class="mb-1">
              <small>Built by Masters for (future) Masters</small>
            </p>
            <p>
              <small>
                <a
                  class="text-link"
                  href="https://twitter.com/storm_lor"
