            // top: 1rem;
            width: 100%;
            height: 100%;
            // background: $gold;
            background: #1c1c1f80;
            // background: #28282880;
            backdrop-filter: blur(8px);
            border-radius: 0.5rem;
          }

          padding: 0.5rem 1rem 1rem 0rem;

          .nav-title {
            display: inline-block;
            padding: 1rem 1rem;
            // padding-bottom: 1rem;
            cursor: default;
            z-index: 1;
          }

          .nav-link {
            z-index: 1;
            display: inline-block;
            transition: transform 0.2s ease;
            &:hover,
            &:focus {
              color: $light-gold;
            }
            &.active {
              color: $dark;
            }
          }
        }
      }

      .display-container {
        .display {
          height: 100vh;
        }
      }
    }
    .right-column {
      .download-container {
      }
    }
  }

  .tracker {
    text-align: center;
  }

  .socials a:hover,
  .socials a:focus,
  a.text-link:hover,
  a.text-link:focus {
    color: $light-gold;
  }

  .btn {
    text-decoration: none;
    color: black;
    padding: 0.8em;
    // background: $gold;

    font-weight: 500;

    .badge {
      font-weight: 400;
    }

    &.download {
    }

    .badge {
      background-color: $background;
    }

    i {
      padding-right: 0.5em;
    }
  }
}

// End of main styles

@include media-breakpoint-up(lg) {
  .main-content {
    .left-column {
      .nav-container {
        left: auto;
        right: calc(var(--bs-gutter-x) * 0.5);
        width: auto;

        .nav {
          display: flex;
          width: auto;
          transform: translateY(calc(50vh - 50%));
          // top: 50%;

          overflow: visible;

          .nav-title {
          }

          .nav-link {
            &.active {
              transform: translateX(-0.8em) translateY(-0.2em);
            }
          }
        }
      }
    }
    .right-column {
      .download-container {
        transform: translateY(calc(50vh - 50%));
        // padding-top: 50vh;
      }
    }
  }
}
</style>
